import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';


@IonicPage()
@Component({
  selector: 'page-info',
  templateUrl: 'info.html',
})
export class InfoPage {

  title     : any;
  subtitle  : any;
  content   : any;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.title = "Desde 1972";
    this.subtitle = "CONSTRUYENDO CONFIANZA";
    this.content = "  Nuestras labores se orientan al diseo, la construccin, operacin y comercializacin de desarrollos inmobiliarios de carcter habitacional, comercial, industrial y de almacenamiento. Damos valor especial a cada proyecto, al realizar un estudio detallado del entorno para descubrir la autntica vocacin del desarrollo a construir, as mismo cuidamos las caractersticas del suelo, su localizacin, vas de acceso, servicios y principalmente el armonizar con el paisaje y concepto arquitectnico de las privadas y sus casas.   De igual manera compaginamos los espacios, diseos y detalles a las necesidades, deseos y gustos del mercado, procurando sobrepasar sus expectativas.   Donde construye Grupo Santa Rita est el desarrollo... frase que confirma, el ser pioneros en la construccin de polos habitacionales, que se han convertido en centros productivos para el crecimiento social y econmico de la poblacin y de la ciudad misma.";
  }
  //----------------------------------------------------------------------------


}



